import React, { useRef, useEffect, useState, Component} from "react";
import './ReceiptCam.css';
import { Link } from 'react-router-dom';
import canvasToImage from "canvas-to-image";


function ReceiptCam(props) {
    const videoRef = useRef(null);
    const photoRef = useRef(null);
    const [hasPhoto, setHasPhoto] = useState(false);

    const getVideo = () => {
        navigator.mediaDevices
            .getUserMedia({
                video: { width: 1920, height: 1080 }
            })
            .then(stream => {
                let video = videoRef.current;
                video.srcObject = stream;

                video.play();
            })
            .catch(err => {
                console.error(err);
            })
    }


    const takePhoto = () => {
        const width = 1920;
        const height = 1080;

        let video = videoRef.current;
        let photo = photoRef.current;

        photo.width = width;
        photo.height = height;

        const ctx = photo.getContext('2d');
        ctx.drawImage(video, 400, 0, width / 2, height / 2)
        const canvasEl = document.getElementById('myCanvas');
        canvasToImage(canvasEl, {
            name: 'myJPG',
            type: 'jpg',
            quality: 0.5
          });
        var target = new Image();
        target.src = canvasEl.toDataURL();
        setHasPhoto(true);
    }


    const closePhoto = () => {
        let photo = photoRef.current;
        let ctx = photo.getContext('2d');
        console.log(ctx);
        ctx.clearRect(0, 400, photo.width, photo.height);
        setHasPhoto(false);
    }


    useEffect(() => {
        getVideo();
    }, [videoRef]);

    return (

        <div className="App" >
            <div className="camera">
                <video ref={videoRef}></video>
                <Link to='/camera/done'> 
                    <button onClick={takePhoto}>SNAP!</button>    
                </Link>
            </div>
            <div className={'result' + (hasPhoto ? 'hasPhoto' : '')} >
                <canvas id="myCanvas" ref={photoRef}></canvas>
                <button onClick={closePhoto}>CLOSE!</button>
            </div>
            
        </div >
    );
}


export default ReceiptCam;